const postData = async(url, data) => { //      
  const result = await fetch(url, { // await    fetch
    method: 'POST', // POST  , GET 
    headers: {'Content-type': 'application/json'}, //    JSON,     formData,   
    body: data, //  ,   GET,   
  });

  return await result.json(); //      PROMISE   JSON
};

const getResource = async(url) => { //      
  const result = await fetch(url); // Fetch  promise

  if (!result.ok) { //     
    throw new Error(`Could not fetch ${url}, status: ${result.status}`); //        
  }

  return await result.json(); //  promise   JSON
};

export {postData};
export {getResource};